.App {
  text-align: center;
}

a, a:active, a:hover {
  text-decoration: none;
  color: white;
  outline: 0;
}

/* Header section */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 6rem;
  z-index: 2;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center; 
  justify-content: space-between; 
  padding: 0 2.5rem; 
  box-sizing: border-box;
}

.header-logo {
  display: flex;
  align-items: center; 
}

.header-name {
  color: #edf2f4;
  border: 1px; 
  font-family: cursive;
  font-style: italic;
  font-size: 30px;
  margin: 0;
}


.header-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
}

.nav-item {
  position: relative;
  display: inline-block;
  font-size: 1.8rem; 
  margin-right: 2rem;
}



.nav-item:after {
  content: "";
  position: absolute;
  width: 100%;
  height: .2rem;
  opacity: 0;
  bottom: -1rem;
  left: 0;
  transition: all .1s ease-in-out;
  background-color: lightgrey;
}

.nav-item:hover:after {
  opacity: 1;
}

/* Body Section */ 
.body {
  position: relative;
  background-color: rgba(25, 94, 14, 0.734);
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  min-height: 56rem;
}

.body .body-container {
  position: relative;
  display: flex;
  max-width: 120rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.body-profile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 20rem;
}

.body-img {
  width: 150%;
  transform: scale(.99);
  border-radius: 50%;
}

.body-content {
  padding-top: 2rem;
  text-align: center;
}

.body-headline {
  font-size: 3rem;
  padding-bottom: 1rem;
  color: white;
}

.body-text {
  font-size: 1.5rem;
  padding-bottom: 1rem;
  color: lightgray;
}

.body-icons {
  display: flex;
  width: 100%;
  justify-content: center;
  font-size: 2.8rem;
}

.body .icon-link {
  padding: 0.8rem 0.8rem;
  color: white;
}

.icon-link:hover {
  color: lightgray;
  transform: scale(1.05);
}
/* End Body Section */ 
/* About Section  */
.about {
  height: 75vh;
  width: 100%;
  padding: 4rem 5rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: wheat;
}

.about-heading {  
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: .3rem;
  color: black;
}

.about-info {
  display: flex;
  justify-content: space-evenly;
}

.about .about-info .about-desc {
  font-size: 1.9rem;
  font-weight: 200; /* Increase font weight */
  line-height: 2rem;
  max-width: 50%;
  margin-top: -0.2rem;
  display: flex;
  align-items: center;
  text-align: justify; /* Justify text */
}

.about .about-img {
    display: block;
}

.about .about-img .about-img-wrapper {
  padding: 15px;
  margin-left: 300px;
  margin-top: 10px;
  height: auto;
  max-width: 400px;
  position: relative;
  display: inline-block;
  border-radius: 1px;
  border: 1vh solid rgba(25, 94, 14, 0.734);
  background-color: gray;
}
.about .about-img .about-img-wrapper img {
  width: 100%;
}
.about .about-img .about-img-wrapper img:hover {
  box-shadow: 0px 2px 10px green;
  transform: translate(-1px, -1px);
}
/* End About Section  */
/* Projects section */
.projects {
  background-color: #e9d5a1;
  height: 70vh;
  margin-top: -2rem;
}
.heading {
  font-size: 2.5rem;
  text-transform: uppercase;
  padding: 1rem;
  letter-spacing: .3rem;
}
.projects-container {
  padding: 3rem;
}
.projects-container .project {
  position: relative;
  height: 45vh; /* Increased height */
  border: 0.3rem solid black;
  border-radius: 20px;
  margin: 0 1rem 0 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem; /* Reduced padding */
}
.projects-container .project .content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 1.2rem;
  height: 100%; /* Adjusted height to fill the project card */
  width: 100%; /* Ensure content takes full width */
  border-radius: 10px;
  transition: all 0.5s;
  cursor: auto;
  overflow: hidden;
  padding: 0.5rem; /* Reduced padding */
}
.content:hover {
  transform: translate(0, -10px);
}
.content:before {
  content: "";
  display: block;
  padding-top: 10%;
}
.projects-container .project .content img {
  display: block;
  margin: 0 auto;
  width: 31%;
}
.name {
  color: #2b2d42;
  margin: 5px 0; /* Reduced margin */
  font-size: 1.6rem;
  margin-bottom: -0.5rem;
}
.description {
  color: #2b2d42;
  font-size: 1.9rem;
  padding: 0.5rem 1rem; /* Reduced padding */
  margin-bottom: 1.5rem;
}
.description-min {
  color: #2b2d42;
  font-size: 1.9rem;
  padding: 0.5rem 1rem; /* Reduced padding */
  margin-bottom: 1.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  line-clamp: 3; 
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.project-button {
  width: 8rem;
  font-size: 1.2rem;
  font-weight: 800;
  padding: 5px 25px;
  border: 1px solid rgba(30, 39, 46, 0.7);
  border-radius: 8%;
  color: rgba(30, 39, 46, 0.7);
}
.project-button:hover,
.project-button:active,
.project-button:focus {
  background-color: #ef233c;
  border-color: #ef233c;
  color: white;
}

@media (min-width: 500px) and (max-width: 650px) { 
  .projects-container .project {
    height: 45vh; /* Increased height */
    padding: 0.5rem; /* Reduced padding */
  }
  .projects-container .project .content {
    margin-top: -1.2rem;
    height: 100%; /* Adjusted height to fill the project card */
    padding: 0.5rem; /* Reduced padding */
  }
  .projects-container .project .content img {
    width: 31%;
  }
  .name {
    font-size: 1.4rem;
    margin-bottom: -0.5rem;
  }
  .description {
    font-size: 1.9rem;
    margin-bottom: 1.2rem;
  }
  .description-min {
    font-size: 1.9rem;
    margin-bottom: 1.2rem;
  }
  .project-button {
    font-size: 1.05rem;
    padding: 3px 25px;
  }
}

@media (min-width: 1000px) { 
  .projects-container {
    padding: 3rem;
  }
  .projects-container .project {
    height: 45vh; /* Increased height */
    padding: 0.5rem; /* Reduced padding */
  }
  .projects-container .project .content {
    margin-top: -1.2rem;
    height: 100%; /* Adjusted height to fill the project card */
    padding: 0.5rem; /* Reduced padding */
  }
  .content:hover {
    transform: translate(0, -10px);
  }
  .content:before {
    padding-top: 10%;
  }
  .projects-container .project .content img {
    width: 31%;
  }
  .name {
    font-size: 2rem;
    margin-bottom: -0.5rem;
  }
  .description {
    font-size: 1.9rem;
    margin-bottom: 1.5rem;
  }
  .description-min {
    font-size: 1.4rem;
    margin-bottom: 1.5rem;
  }
  .project-button {
    font-size: 1.2rem;
    padding: 4px 25px;
  }
}

@media (min-width: 2000px) { 
  .projects-container {
    padding: 3rem;
  }
  .projects-container .project {
    height: 45vh; /* Increased height */
    padding: 0.5rem; /* Reduced padding */
  }
  .projects-container .project .content {
    margin-top: -1.2rem;
    height: 100%; /* Adjusted height to fill the project card */
    padding: 0.5rem; /* Reduced padding */
  }
  .content:hover {
    transform: translate(0, -10px);
  }
  .content:before {
    padding-top: 10%;
  }
  .projects-container .project .content img {
    width: 35%;
  }
  .name {
    font-size: 1.6rem;
    margin-bottom: -0.2rem;
  }
  .description {
    font-size: 1.2rem;
    margin-bottom: 1.7rem;
  }
  .description-min {
    font-size: 1.2rem;
    margin-bottom: 1.7rem;
  }
  .project-button {
    font-size: 1.4rem;
    padding: 7px 25px;
  }
}

/* Skills section */
.skills {
  background-color: white;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  font-size: 4rem;
  color: black;
  margin: 3rem 0;
  text-align: center;
  margin-top: 5%;
  letter-spacing: 0.3rem;
}

.skill-cards {
  background-color: gray;
  padding: 1.2rem 1rem 1rem 1rem;
  margin: 1.2rem;
  width: 15rem;
  border-radius: 8px;
  color: #ffffff;
}

.skill-icon {
  color: white;
  font-size: 40px;
}

.skill-holder {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-evenly;
  align-items: center;
  margin: 2em 0;
}

.skill {
  font-size: 1.3rem;
  font-weight: bolder;
  text-align: center;
  text-shadow: 2px 3px 4px #000000;
  text-transform: uppercase;
  font-style: normal;
}

.skill-cards:hover {
  box-shadow: 0px 2px 10px #000000;
}

@media (max-width: 500px) { 
  .title {
    font-size: 4rem;
    color: black;
    margin: 3rem 0;
    text-align: center;
    margin-top: 8%;
    letter-spacing: 0.3rem;
  }

  .skill-cards {
    background-color: gray;
    padding: 1.2rem 1rem 1rem 1rem;
    margin: 0.2rem;
    width: 7em;
    border-radius: 8px;
    color: #ffffff;
  }

  .skill-icon {
    color: white;
    font-size: 40px;
  }

  .skill-holder {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    justify-content: space-evenly;
    align-items: center;
    margin: 4em 0;
  }

  .skill {
    padding: 0;
    font-size: 0rem;
  } 
} 
/* End Skills section */
/* Start Footer section */
.footer {
  min-height: 20vh;
  width: 100%;
  background-color: rgb(8, 68, 8);
  color: white;
  flex-direction: column;
  text-align: center;
  padding: 5rem;
}
.footer .footer-contact-info {
  padding: 20px;
}
.footer .footer-contact-info .footer-heading {
  font-size: 2.5rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
}
.footer .footer-contact-info .footer-contact-access {
  font-size: 1.4rem;
  padding-top: 10px;
  letter-spacing: .2rem;
margin-bottom: -0.1rem;
}
.footer .social-icons a {
  color: white;
  font-size: 3rem;
  margin: 0.7rem;
  display: inline-block;
}
/* End Footer section  */
